import React from "react";
import "./App.css";
import { BrowserRouter as Router, Switch, Route, NavLink, Redirect, useLocation } from "react-router-dom";
import { RecipeCreate } from "./RecipeCreate";
import { RecipeEdit } from "./RecipeEdit";
import { RecipeSingle } from "./RecipeSingle";
import { RecipeList } from "./RecipeList";
import { Home } from "./Home";

function App() {
  return (
    <Router>
      <nav className="navbar navbar-expand-sm navbar-dark bg-primary mb-3">
        <div className="" id="navbarNav">
          <ul className="navbar-nav lead font-weight-bold">
            <li className="nav-item">
              <NavLink to={`/`} activeClassName="active" exact>
                <span className="nav-link">
                  <img width="60px" src={`${process.env.REACT_APP_BACKEND_URL}/static/assets/logo.png`} alt="" />
                </span>
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink to={`/react-kurzus/fejlessz-onalloan?url=/receptek`} activeClassName="active" exact>
                <span className="nav-link pt-4">Receptek</span>
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink to={`/react-kurzus/fejlessz-onalloan?url=/uj-recept`} activeClassName="active">
                <span className="nav-link pt-4">Új recept</span>
              </NavLink>
            </li>
          </ul>
        </div>
      </nav>
      <div className="container" style={{ marginTop: "140px" }}>
      <Switch>
        <Route component={WrapperComponent} path="/react-kurzus" />
        <Redirect to={"/react-kurzus/fejlessz-onalloan?url=/"} />
      </Switch>
      </div>
    </Router>
  );
}

function WrapperComponent(props) {
  const fakeURL = new URLSearchParams(useLocation().search).get("url");

  if (!fakeURL) {
    return <Redirect to={"/react-kurzus/fejlessz-onalloan?url=/"} />;
  }

  if (fakeURL === "/") {
    return <Home {...props} />;
  }
  if (fakeURL === "/receptek") {
    return <RecipeList {...props} />;
  }
  if (fakeURL === "/uj-recept") {
    return <RecipeCreate {...props} />;
  }
  if (fakeURL.includes("/recept-szerkesztes/")) {
    return <RecipeEdit recipeSlug={fakeURL.split("/")[2]} />;
  }
  if (fakeURL.includes("/recept/")) {
    return <RecipeSingle recipeSlug={fakeURL.split("/")[2]} />;
  }

  return <Redirect to={"/react-kurzus/fejlessz-onalloan?url=/"} />;
}


export default App;
